<template>
  <loading :active="isLoading"></loading>
  <div class="background ">
	<div class="container" >
	  <form class="row d-flex justify-content-center " @submit.prevent="signIn">
		<div class="col-md-6 p-5 border bg-dark opacity-75 border-5 rounded-5 text-white fs-5">
		  <h1 class="mb-3 text-center">後台登入</h1>
		  <div class="mb-2">
			<label for="inputEmail" class="sr-only">帳號</label>
			<input
			  type="email"
			  id="inputEmail"
			  class="form-control"
			  placeholder="Email address"
			  required
			  v-model="user.username"
			/>
		  </div>
		  <div class="mb-2">
			<label for="inputPassword" class="sr-only">密碼</label>
			<input
			  type="password"
			  id="inputPassword"
			  class="form-control"
			  placeholder="Password"
			  required
			  v-model="user.password"
			/>
		  </div>
		  <div class="d-flex justify-content-between mt-4">
			<button class="btn btn-primary btn-block w-30" type="submit">
			  登入後台
			</button>
			<router-link to="/" class="btn btn-danger btn-block w-30 " type="button">
			  返回前台
			</router-link>
		  </div>
		</div>
	  </form>
	</div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      user: {
        username: "",
        password: "",
      },
      isLoading: false,
    };
  },
  methods: {
    signIn() {
      this.isLoading = true;
      const api = `${process.env.VUE_APP_API}admin/signin`;
      this.$http
        .post(api, this.user)
        .then((res) => {
          this.isLoading = false;
          if (res.data.success) {
            // 建立token
            const { token, expired } = res.data;
            document.cookie = `token = ${token};expires = ${new Date(expired)}`;
            this.$router.push("/admin/products");
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>
<style lang="scss" scoped>
.background{
	height: 100vh;
	background-image:url(@/assets/images/coffee_banner.jpg);
	background-repeat: no-repeat;
	background-position: center center;
	background-size: cover;
	display: flex;
  justify-content: center;
  align-items: center;
}
</style>
